import { NextAuthOptions } from "next-auth";
import CredentialsProvider from "next-auth/providers/credentials";
import { compare } from "bcryptjs";
import { promises as fs } from 'fs';
import path from 'path';
import { hash } from 'bcryptjs';

const usersFilePath = path.join(process.cwd(), 'src/lib/users.json');

interface User {
  id: string;
  name: string;
  email: string;
  password: string;
}

interface UsersData {
  users: User[];
}

async function ensureUsersFile() {
  try {
    await fs.access(usersFilePath);
  } catch {
    // File doesn't exist, create it
    await fs.writeFile(usersFilePath, JSON.stringify({ users: [] }, null, 2));
  }
}

async function findUserByEmail(email: string): Promise<User | null> {
  await ensureUsersFile();
  const fileContent = await fs.readFile(usersFilePath, 'utf-8');
  const data: UsersData = JSON.parse(fileContent);
  return data.users.find(user => user.email === email) || null;
}

export const authOptions: NextAuthOptions = {
  providers: [
    CredentialsProvider({
      name: "credentials",
      credentials: {
        email: { label: "Email", type: "email" },
        password: { label: "Password", type: "password" },
      },
      async authorize(credentials) {
        if (!credentials?.email || !credentials?.password) {
          throw new Error("Invalid credentials");
        }

        const user = await findUserByEmail(credentials.email);

        if (!user || !user.password) {
          throw new Error("Invalid credentials");
        }

        const isPasswordValid = await compare(credentials.password, user.password);

        if (!isPasswordValid) {
          throw new Error("Invalid credentials");
        }

        return {
          id: user.id,
          email: user.email,
          name: user.name,
        };
      },
    }),
  ],
  session: {
    strategy: "jwt",
  },
  pages: {
    signIn: "/login",
  },
  callbacks: {
    async session({ token, session }) {
      if (token && session.user) {
        session.user.id = token.id as string;
        session.user.name = token.name as string;
        session.user.email = token.email as string;
      }
      return session;
    },
    async jwt({ token, user }) {
      const dbUser = await findUserByEmail(token.email as string);

      if (!dbUser) {
        if (user) {
          token.id = user?.id;
        }
        return token;
      }

      return {
        id: dbUser.id,
        name: dbUser.name,
        email: dbUser.email,
      };
    },
  },
};

export async function registerUser(name: string, email: string, password: string) {
  try {
    console.log("Starting user registration:", { email });
    
    // Ensure users file exists
    await ensureUsersFile();
    
    // Read users file
    const fileContent = await fs.readFile(usersFilePath, 'utf-8');
    const data: UsersData = JSON.parse(fileContent);

    // Check if user already exists
    if (data.users.some(user => user.email === email)) {
      console.log("User already exists:", email);
      throw new Error('User already exists');
    }

    // Hash password
    console.log("Hashing password");
    const hashedPassword = await hash(password, 10);

    // Create new user
    const newUser: User = {
      id: Math.random().toString(36).substring(7),
      name,
      email,
      password: hashedPassword,
    };

    // Add user to the list
    data.users.push(newUser);

    // Save back to file
    console.log("Saving user to file");
    await fs.writeFile(usersFilePath, JSON.stringify(data, null, 2));

    console.log("User registered successfully:", { id: newUser.id, email: newUser.email });
    return newUser;
  } catch (error) {
    console.error("Error in registerUser:", error);
    throw error;
  }
}

export async function verifyUser(email: string, password: string) {
  try {
    console.log("Starting user verification:", { email });
    
    // Ensure users file exists
    await ensureUsersFile();
    
    // Read users file
    const fileContent = await fs.readFile(usersFilePath, 'utf-8');
    const data: UsersData = JSON.parse(fileContent);

    const user = data.users.find(user => user.email === email);
    if (!user) {
      console.log("User not found:", email);
      throw new Error('User not found');
    }

    const isValid = await compare(password, user.password);
    if (!isValid) {
      console.log("Invalid password for user:", email);
      throw new Error('Invalid password');
    }

    console.log("User verified successfully:", { id: user.id, email: user.email });
    return user;
  } catch (error) {
    console.error("Error in verifyUser:", error);
    throw error;
  }
} 